.form__container {
  @media (min-width: $desktop-width) {
    max-width: 1220px;
    margin: 0 auto;
    padding-top: 60px;

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px 80px;
  }
}

.form__fieldset {
  width: 100%;
  min-width: 320px;
  max-width: 1220px;
  margin: 0 auto;
  padding: 22px 20px;

  border: none;

  @media (min-width: $tablet-width) {
    padding: 22px 67px 22px 70px;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
  }
}

.form__container .form__fieldset {
  border-bottom: 1px solid #d9d9d9;

  @media (min-width: $tablet-width) {
    border: none;
  }

  @media (min-width: $desktop-width) {
    //padding: 22px 0 0;
    padding-top: 5px;
  }
}
