.form__legend {
  margin-top: 4px;
  margin-bottom: 32px;

  line-height: 27px;

  @media (min-width: $tablet-width) {
    margin: 3px -67px 70px -70px;
    padding-left: 28px;

    font-size: 36px;
    line-height: 48px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 70px;
  }

  @media (min-width: 1440px) {
    margin: 3px -100px 70px -110px;
  }
}

.form__legend span {
  display: inline-block;
  max-width: 222px;

  @media (min-width: $tablet-width) {
    max-width: 100%;
    padding: 0 40px;
  }
}

.form__legend::after,
.form__legend::before {
  @media (min-width: $tablet-width) {
    top: 28px;
  }
}
